.cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .cursor {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    border: var(--warna-pink-tua) solid 1px;
  }
}

.fotografipage-container {
  background-color: var(--warna-biru);
}
.fotografipage-container.navbar-aktif .c-scrollbar_thumb {
  display: none;
}
.fotografipage-container .fotografer-wrapper {
  z-index: 0;
  position: absolute;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.fotografipage-container .fotografer-wrapper h1 {
  overflow: hidden;
  text-transform: uppercase;
  display: flex;
  font-size: 8vw;
  color: var(--warna-cream);
}
@media screen and (min-width: 64rem) {
  .fotografipage-container .fotografer-wrapper h1 {
    font-size: 4vw;
  }
}
.fotografipage-container .wrapper:nth-child(1) {
  border-left: unset;
}
.fotografipage-container .wrapper {
  border-left: 1px solid var(--warna-pink-tua);
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
}
.fotografipage-container .wrapper h1 {
  padding-top: 5rem;
  text-transform: uppercase;
  width: 100%;
  font-size: 4rem;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .wrapper h1 {
    padding-top: 8rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .fotografipage-container .wrapper h1 {
    font-size: 6rem;
  }
}
.fotografipage-container .wrapper .foto-wrapper {
  flex-wrap: nowrap;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300vw;
  padding-bottom: 3rem;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .wrapper .foto-wrapper {
    width: 100vw;
  }
}
.fotografipage-container .wrapper .foto-wrapper .foto {
  border: 1px solid var(--warna-pink-tua);
  margin: 0 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  height: 80vw;
  width: 80vw;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto {
    width: 18rem;
    height: 18rem;
    margin: 0 2rem;
  }
}
@media screen and (min-width: 64rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto {
    width: 26rem;
    height: 26rem;
    margin: 0 2rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto {
    width: 38rem;
    height: 38rem;
    margin: 0 2rem;
  }
}
.fotografipage-container .wrapper .foto-wrapper .foto .psoedo {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background-color: var(--warna-pink);
  z-index: 3;
}
.fotografipage-container .wrapper .foto-wrapper .foto img:not(.makan-dulu) {
  z-index: 1;
  height: 80vw;
  width: 80vw;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto img:not(.makan-dulu) {
    width: 18rem;
    height: 18rem;
  }
}
@media screen and (min-width: 64rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto img:not(.makan-dulu) {
    width: 26rem;
    height: 26rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto img:not(.makan-dulu) {
    width: 38rem;
    height: 38rem;
  }
}
.fotografipage-container .wrapper .foto-wrapper .foto .placeholder {
  z-index: 2;
  position: absolute;
}
.fotografipage-container .wrapper .foto-wrapper .foto .makan-dulu {
  width: 80vw;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto .makan-dulu {
    width: 18rem;
  }
}
@media screen and (min-width: 64rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto .makan-dulu {
    width: 26rem;
  }
}
@media screen and (min-width: 87.5rem) {
  .fotografipage-container .wrapper .foto-wrapper .foto .makan-dulu {
    width: 38rem;
  }
}
.fotografipage-container .pink-container {
  width: 600% !important;
  max-width: 600% !important;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .pink-container {
    width: 200% !important;
    max-width: 200% !important;
  }
}
.fotografipage-container .pink-container h1 {
  color: var(--warna-pink-tua);
}
.fotografipage-container .kuning-container {
  overscroll-behavior: none;
  width: 900% !important;
  max-width: 900% !important;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .kuning-container {
    width: 300% !important;
    max-width: 300% !important;
  }
}
.fotografipage-container .kuning-container h1 {
  color: var(--warna-kuning-tua);
}
.fotografipage-container .coklat-container {
  overscroll-behavior: none;
  width: 900% !important;
  max-width: 900% !important;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
}
@media screen and (min-width: 40rem) {
  .fotografipage-container .coklat-container {
    width: 300% !important;
    max-width: 300% !important;
  }
}
.fotografipage-container .coklat-container h1 {
  color: var(--warna-coklat-tua);
}

[data-scroll-container] {
  overflow-y: hidden !important;
  overflow-x: hidden !important;
}